@use '../../../config/variables';
@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

.product-card {
  cursor: pointer;
  box-sizing: border-box;
  &.hover {
    @include variables.themed() {
      background-color: variables.t('card-bg');
    }
  }
  &__image {
    width: 100%;
    height: 100%;
    &.hover {
      opacity: 0.75;
    }
  }
  &__image-container {
    padding: 5px;
  }
  &__name {
    width: 100%;
    text-align: center;
    &.hover {
      @include variables.themed() {
        color: variables.t('option');
      }
    }
  }
  &__price {
    width: 100%;
    text-align: center;
    font-weight: 600;
    &.hover {
      @include variables.themed() {
        color: variables.t('option');
      }
    }
  }
}

.product-list-container {
  box-sizing: border-box;
  max-width: 1400px;
  margin: auto;
  padding: 20px;
}

.product-row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.page-title {
  font-family: 'Patrick Hand', cursive;
  font-weight: 300;
  @include variables.themed() {
    color: variables.t('text');
  }
  line-height: 1.2em;
}

.pagination-el {
  &__button {
    @include variables.themed() {
      color: variables.t('text');
    }
    &.Mui-selected {
      @include variables.themed() {
        background-color: unquote(variables.t('text')+'20');
      }
    }
    &__container {
      min-width: 70px;
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      &__prev {
        padding-right: 5px;
      }
      &__next {
        padding-left: 5px;
      }
    }
  }
}

.filter-item {
  min-width: 70px;
  display: flex;
  flex-wrap: nowrap;
}