@use '../../../config/variables';
@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

@media (max-width: 749px) {
  .image-banner {
    height: 180px;
    img {
      height: 180px;
    }
  }
  .home-intro {
    h1 {
      font-size: 64px;
    }
    h2 {
      font-size: 24px;
    }
  }
}

@media (min-width: 750px) {
  .image-banner {
    height: 300px;
    img {
      height: 300px;
    }
  }
  .home-intro {
    h1 {
      font-size: 72px;
    }
    h2 {
      font-size: 32px;
    }
  }
}

@media (min-width: 1200px) {
  .image-banner {
    height: 400px;
    img {
      height: 400px;
    }
  }
}

.banner-container {
  padding: 0 !important;
}

.image-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
  img {
    display: block;
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
}

.home-intro {
  margin: 30px;
  text-align: center;
  @include variables.themed() {
    color: variables.t('text');
    background-color: variables.t('bg');
  }
  h1 {
    font-family: 'Patrick Hand', cursive;
    font-weight: 300;
    line-height: 1.2em;
    letter-spacing: 0.0625em;
    margin: 0;
  }
  h2 {
    margin-top: 8px;
    font-family: 'Patrick Hand', cursive;
    font-weight: 200;
    line-height: 1.2em;
    letter-spacing: 0.0625em;
  }
}

.home-container {
  @include variables.themed() {
    color: variables.t('text');
    background-color: variables.t('bg');
  }
}