@import url('https://fonts.googleapis.com/css2?family=Mate+SC&display=swap');

@mixin respond-at($media) {
  @media only screen and (min-width: $media){ @content; }
}

.btn {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  width: auto;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 2px;
  background-color: #49547b;
  color: #dda293;
  font-family: Helvetica,"Helvetica Neue",Arial,"Lucida Grande",sans-serif;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .08em;
  white-space: normal;
  font-size: 13px;
  padding: 8px 15px;
  &:focus, &:not([disabled]):hover {
    color: #dda293;
    background-color: #363e5b;
  }
  @include respond-at(750px){
    padding: 10px 18px;
  }
}

.hero {
  position: relative;
  display: table;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-color: rgba(0,0,0,calc(0 * 0.1)) !important;
  background-blend-mode: overlay;
  &__btn {
    margin-top: 27.5px;
    outline: none !important;
  }
  &__inner{
    position: relative;
    display: table-cell;
    vertical-align: middle;
    padding: 55px 0;
    z-index: 2;
    h1.mega-title {
      color: #ffffff;
    }
  }
  &__overlay::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #685858;
    opacity: .4;
    z-index: 1;
  }
  &__wrapper--medium {
    overflow: hidden;
  }
}

