$themes: (
  default: (
    // logo: url("../images/brand/logo_vertical.svg"),
    bg: $bg--light,
    card-bg: $bg--card--light,
    text: $text--light,
    text-secondary: $text--2--light,
    option: $brand--blue--light,
    disabled: $disabled--light,
    link: $brand--blue--light,
    hover: $hover--light,
    border: $border--light,
    danger: $danger--light
  ),
  dark: (
    // logo: url("../images/brand/logo_vertical--invert.svg"),
    bg: $bg--dark,
    card-bg: $bg--card--dark,
    text: $text--dark,
    text-secondary: $text--2--dark,
    option: $brand--blue--dark,
    disabled: $disabled--dark,
    link: $brand--blue--dark,
    hover: $hover--dark,
    border: $border--dark,
    danger: $danger--dark
  ),
);

@mixin themed() {
  @each $theme, $map in $themes {
    .theme--#{$theme} & {
      $theme-map: () !global;
      @each $key, $submap in $map {
        $value: map-get(map-get($themes, $theme), '#{$key}');
        $theme-map: map-merge($theme-map, ($key: $value)) !global;
      }
      @content;
      $theme-map: null !global;
    }
  }
}

@function t($key) {
  @return map-get($theme-map, $key);
}
