@use '../../../config/variables';

@media screen and (min-width: 750px) {
  .price--large {
      font-size: 24px;
  }
}

.product-container {
  margin-top: 20px !important;
}
.product-info {
  *:first-child {
    margin-top: 0;
  }
}
.product-title {
  word-break: break-word;
  margin-bottom: 1.5rem;
  max-width: 70vw;
}

.product-description {
  max-width: 70vw;
}

.product-image {
  width: 100%;
}

.price--large {
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
  margin-bottom: 1.5rem;
}

.option-label {
  border: 1px solid;
  border-radius: 30px;
  padding: 7px 20px;
  margin-bottom: 0;
  &__default{
    @include variables.themed() {
      border-color: variables.t('option');
      color: variables.t('option');
      background-color: variables.t('bg');
    }
  }
  &__selected{
    @include variables.themed() {
      border-color: variables.t('option');
      color: variables.t('bg');
      background-color: variables.t('option');
    }
  }
  &__disabled{
    @include variables.themed() {
      border-color: variables.t('disabled');
      color: variables.t('disabled');
      background-color: variables.t('bg');
    }
    &:hover {
      cursor:not-allowed;
    }
  }
}

.options-title {
  font-size: 12px;
  @include variables.themed() {
    color: variables.t('text-secondary') !important;
  }
  p {
    margin-bottom: 0;
  }
}

.options-container {
  flex-direction: row !important;
}

.radio-button {
  padding: 0 !important;
  .PrivateSwitchBase-input-4 {
    height: 0 !important;
  }
}

.no-radio-option {
  margin: 7px 5px 2px 0 !important;
}
