@use '../../config/variables';

.announcement-bar {
  z-index: 10;
  position: relative;
  text-align: center;
  border-bottom: 1px solid transparent;
  padding: 2px;
  background-color: variables.$tertiary-color-light;
  &__message {
    display: block;
    color: variables.$secondary-color-dark;
    padding: 11px 22px;
    font-size: 1.06667em;
    font-weight: 400;
    margin: 0px;
    @media (min-width: 750px) {
      padding-left: 55px;
      padding-right: 55px;
    }
  }
}