@use '../../config/variables';

a {
  @include variables.themed() {
    color: variables.t('link');
  }
  text-decoration: none;
  &:focus, &:not([disabled]):hover{
    @include variables.themed() {
      color: variables.t('hover');
    }
  }
}

.btn--link {
  background-color: transparent;
  border: 0;
  margin: 0;
  @include variables.themed() {
    color: variables.t('link');
  }
  text-align: left;
}

.icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  fill: currentColor;
}

.list--inline {
  padding: 0;
  margin: 0;
  li {
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
  }
}

.site-header {
  top: 0;
  width: 100%;
  z-index:2;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px 0px;
  @include variables.themed() {
    background-color: variables.t('card-bg');
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px 0px;
  }
  .grid{
    @include variables.themed() {
      background-color: variables.t('card-bg');
    }
  }
  @media (min-width: 750px) {
    padding: 0 55px;
  }
  &__scrolled {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 8px 0px;
  }
  &__account, &__cart, &__search, &__darkmode, &__menu, &__sidebar {
    position: relative;
    padding: 10px 11px;
    @include variables.themed() {
      color: variables.t('text');
    }
  }
  &__mobile-nav {
    padding: 0 20px;
    z-index: 11;
    position: relative;
    @include variables.themed() {
      background-color: variables.t('card-bg');
    }
  }
  &__icon {
    .icon-search {
      color: variables.$text-color;
    }
  }
  &__icons {
    position: relative;
    white-space: nowrap;
    &-wrapper {
      position: relative;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      -ms-flex-align: center;
      -webkit-align-items: center;
      -moz-align-items: center;
      -ms-align-items: center;
      -o-align-items: center;
      align-items: center;
      -webkit-justify-content: flex-end;
      -ms-justify-content: flex-end;
      justify-content: flex-end;
    }
    &-wrapper-left {
      position: relative;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      -ms-flex-align: center;
      -webkit-align-items: center;
      -moz-align-items: center;
      -ms-align-items: center;
      -o-align-items: center;
      align-items: center;
      -webkit-justify-content: flex-start;
      -ms-justify-content: flex-start;
      justify-content: flex-start;
    }
  }
  &__logo {
    margin: 15px 0px;
    width: max-content;
    &-image {
      margin: 0;
    }
  }
}

.site-nav {
  position: relative;
  padding: 0;
  text-align: center;
  margin: 25px 0;
  a {
    padding: 3px 10px;
    text-decoration: none;
  }

  &__label {
    border-bottom: 1px solid transparent;
    @include variables.themed() {
      color: variables.t('text');
    }
    font-weight: 600;
    &:hover {
      @include variables.themed() {
        border-bottom-color: variables.t('text');
      }
    }
  }

  &__link {
    display: block;
    white-space: nowrap;
    &--active {
      .site-nav__label {
        @include variables.themed() {
          color: variables.t('text');
          border-bottom-color: variables.t('text');
        }
      }
    }
  }

  &__sidebar {
    a {
      @include variables.themed() {
        background-color: variables.t('card-bg');
      }
    }
    @include variables.themed() {
      background-color: variables.t('card-bg');
    }
  }
}

.text-right {
  text-align: right !important;
}