@use '../../config/variables';

@media (min-width: 750px) {
  .site-footer {
    margin-top: 100px;
    &__item {
      padding: 0 15px 0 15px;
      margin-bottom: 45px;
      &--center {
        -webkit-justify-content: center;
        -ms-justify-content: center;
        justify-content: center;
        & > * {
          text-align: center;
        }
      }
      &--full-width {
        -webkit-flex: 1 1 100%;
        -moz-flex: 1 1 100%;
        -ms-flex: 1 1 100%;
        flex: 1 1 100%;
      }
    }
    &__linklist-item {
      padding: 0 30px 5px 0;
    }
  }
}
@media (max-width: 749px) {
  .site-footer {
    &__item {
      &:last-of-type {
          margin-bottom: 0;
      }
    }
    &__linklist-item {
      padding: 15px 0;
    }
  }
}

ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}

.list--inline {
  padding: 0;
  margin: 0;
  &>li {
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
  }
}

.page-width {
  max-width: min(1200px, 98vw);
  margin: 0 auto;
  padding-left: 55px;
  padding-right: 55px;
  @media (max-width: 749px) {
    padding-left: 22px;
    padding-right: 22px;
  }
}

.site-footer {
  @include variables.themed() {
    color: variables.t('text');
    background-color: variables.t('card-bg');
  }
  & p,
  &__linklist-item a,
  &__rte a,
  & h4,
  & small,
  &__copyright-content a,
  &__newsletter-error,
  &__social-icons .social-icons__link {
    @include variables.themed() {
      color: variables.t('text');
    }
  }
  &__content {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-align: start;
    -webkit-align-items: flex-start;
    -moz-align-items: flex-start;
    -ms-align-items: flex-start;
    -o-align-items: flex-start;
    align-items: flex-start;
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    @include variables.themed() {
      background-color: variables.t('card-bg');
    }
  }
  &__copyright-content {
    font-size: .8em;
    display: flex;
    justify-content: center;
  }
  &__item {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-flex: 1 1 100%;
    -moz-flex: 1 1 100%;
    -ms-flex: 1 1 100%;
    flex: 1 1 100%;
    margin-bottom: 55px;
    &:first-of-type {
      padding-left: 0;
    }
    &:last-of-type {
      padding-right: 0;
    }
  }
  &__linklist-item {
    display: block;
  }
}